import React, { useState, useEffect, useLayoutEffect } from 'react';
import debounce from 'lodash.debounce';
import cls from 'classnames';

import Main from './sections/Main'
import Sidebar from './sections/Sidebar'
import styles from './index.module.scss'

const useWindowSize = () => {
    const [size, setSize] = useState(0);

    useLayoutEffect(() => {
        function updateSize() {
            setSize(window.innerWidth);
        }

        window.addEventListener('resize', debounce(() => {
            updateSize()
        }, 100));

        updateSize();

        return () => window.removeEventListener('resize', updateSize);
    }, []);
    return size;
}

const MainPage = () => {
    const isMobile = useWindowSize() < 1024;
    const [favouriteJokes, setJokes] = useState([]);
    const [opened, toggle] = useState(false);

    const onLike = (data, toggle) => {
        let favourites = JSON.parse(localStorage.getItem('favourites'));

        if (!favourites) {
            favourites = []
        }

        favourites.unshift(data);

        localStorage.setItem('favourites', JSON.stringify(favourites));
        setJokes(favourites);
        toggle();
    }

    const onDislike = (id, toggle) => {
        let favourites = JSON.parse(localStorage.getItem('favourites'));

        favourites.splice(favourites.findIndex(joke => joke.id === id), 1)

        localStorage.setItem('favourites', JSON.stringify(favourites));
        setJokes(favourites);
        toggle();
    }

    useEffect(() => {
        setJokes(JSON.parse(localStorage.getItem('favourites')))
    }, []);

    return (
        <>
            <div className={cls(opened && isMobile && styles.opened)}>
                <div className={styles.wrapper} style={{ height: isMobile && opened ? '-webkit-fill-available' : '' }}>
                    <Main onLike={onLike} onDislike={onDislike} favouriteJokes={favouriteJokes} toggle={toggle} />
                    <div className={styles.sidebar}>
                        <Sidebar jokes={favouriteJokes} onDislike={onDislike} />
                    </div>
                </div>
            </div>
            <div style={{ display: isMobile && opened ? 'block' : 'none' }}>
                <Sidebar jokes={favouriteJokes} onDislike={onDislike} opened={isMobile && opened} toggle={toggle} />
            </div>

        </>
    );
}

export default MainPage;