import React from 'react';

import Joke from '$components/Joke'
import { ReactComponent as ButtonOpened } from '$svg/buttonOpened.svg';

import styles from './index.module.scss'

const Sidebar = ({ jokes, onDislike, opened = false, toggle }) => {
    return (
        <div className={styles.wrapper}>
            {!opened && <div className={styles.header}>Favourite</div>}
            {opened && <div className={styles.burgerBtn} onClick={() => toggle(false)}>
                    <ButtonOpened />
                    Favourite
                </div>}
            <div className={styles.jokes}>
                {jokes && jokes.length !== 0 ?
                    jokes.map((joke, index) => (
                        <Joke 
                            key={joke.id}
                            data={joke}
                            forFavourite={true}
                            hideCategory={true}
                            onDislike={onDislike}
                            favourite={true}
                        />
                    )) : <div>still no favourite jokes?</div>
                }
            </div>
        </div>
    );
}

export default Sidebar;